import React from 'react';

export const ACTION_TYPE = Object.freeze({
  TOOK: 'hand',
  KILLED: 'sword',
});

export default function StatSVG({ stat }) {
  switch (stat) {
    case ACTION_TYPE.TOOK:
      return Hand();
    case ACTION_TYPE.KILLED:
      return Sword();
    default:
      return Sword();
  }
}

