import React from 'react';

export const DRAKE_TYPE = Object.freeze({
  OCEAN: 'ocean',
  INFERNAL: 'infernal',
  MOUNTAIN: 'mountain',
  CLOUD: 'cloud',
  ELDER: 'elder',
});

export default function DrakeSVG({ type }) {
  switch (type) {
    case DRAKE_TYPE.OCEAN:
      return OceanSVG();
    case DRAKE_TYPE.INFERNAL:
      return InfernalSVG();
    case DRAKE_TYPE.MOUNTAIN:
      return MountainSVG();
    case DRAKE_TYPE.CLOUD:
      return CloudSVG();
    case DRAKE_TYPE.ELDER:
      return ElderSVG();
    default:
      return CloudSVG();
  }
}

